import React from 'react';

export default React.createClass({
  render() {
    if( this.props.correct===0 ) {
      //If question has not been answered render nothing
      return (
        <div></div>
      );
    } else if( this.props.correct===1 ) {
      //If question has been answered correctly render green "correct"
      return (
        <p className="correct"><b>Correct</b></p>
      );
    }else {
      //If question has been answered incorrectly render red "incorrect"
      return (
        <p className="incorrect"><b>Incorrect</b></p>
      );
    }
  }
});



