import React from 'react';
import renderHTML from 'react-render-html';

export default React.createClass({
  render() {
    if(this.props.full[1]&&this.props.correct===0) {
      //if it isn't question 4 and the user hasn't answered it
      return(
        <table>
            <tr>
                <td className="squareb">a</td>
                <td className="square">{renderHTML(this.props.full[1][0])}</td>
                <td className="square">{renderHTML(this.props.full[1][1])}</td>
            </tr>
            <tr>
                <td className="square">{renderHTML(this.props.full[1][2])}</td>
                <td className="square">{renderHTML(this.props.full[1][3])}</td>
                <td className="square">{renderHTML(this.props.full[1][4])}</td>
            </tr>
            <tr>
                <td className="square">{renderHTML(this.props.full[1][5])}</td>
                <td className="square">{renderHTML(this.props.full[1][6])}</td>
                <td className="square">{renderHTML(this.props.full[1][7])}</td>
            </tr>
        </table>
      );
    } else if(this.props.correct===0){
      //if it is question 4 and the user hasn't answered it
      return(
        <div>
        <img src="meese.jpeg" alt="Not sentient"/>
        <img src="moose.jpeg" alt="Sentient"/>
        <img src="meerse.jpeg" alt="Not sentient"/>
        </div>
      );
    } else {
      //if the user has answered it
        return(
          <div></div>
        );
    };
  }
});



