import React from 'react';

export default React.createClass({
  render() {
    //this.props.answers is the number of correct answers
    //this.props.place is the number of questions (origin = 0)
    return(
      <p className="score">{this.props.answers}/{this.props.place+1}</p>
    );
  }
});



